export const initQuizzes = () => {
  ;(function (
    window: Window,
    document: Document,
    scriptTagName: string,
    scriptSrc: string,
    scriptVersion: string,
    globalObjectName: string,
  ) {
    interface MessageQueueFunction {
      (...args: unknown[]): void
      q?: unknown[][]
    }

    const globalObject: MessageQueueFunction = (window[globalObjectName] =
      window[globalObjectName] ||
      function (...args: unknown[]) {
        ;(globalObject.q = globalObject.q || []).push(args)
      })

    const scriptElement = document.createElement(scriptTagName) as HTMLScriptElement
    scriptElement.src = `${scriptSrc}?v=${scriptVersion}`
    scriptElement.type = 'module'
    scriptElement.async = true

    document.head.appendChild(scriptElement)
  })(window, document, 'script', import.meta.env.WIDGET_SCRIPT_URI, import.meta.env.WIDGET_VERSION, 'mqzzs')

  mqzzs('init', {apiEndpoint: import.meta.env.WIDGET_API_ENDPOINT})
}
